import "./works.scss";
import { useState } from "react";

export default function Works() {
  const [currentSlide, setCurrentSlide] = useState(0);
  const data = [
    {
      id: "1",
      icon: "./assets/mobile.png",
      title: "Web Design",
      desc: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. ",
      img: "https://99designs-blog.imgix.net/blog/wp-content/uploads/2018/10/attachment_100040756-e1538485934255.jpeg?auto=format&q=60&fit=max&w=930",
      link:"#intro"
    },
    {
      id: "2",
      icon: "./assets/globe.png", 
      title: "Mobile Application",
      desc: "Responsible for translating code into user-friendly applications. Mostly I contribute to the design, testing, releasing and support of the application",
      img: "https://i.pinimg.com/originals/e9/c9/2f/e9c92f7869d682a6fa5a97fb8a298f30.jpg",
      link:"#"
    },
    {
      id: "3",
      icon: "./assets/writing.png",
      title: "Graphic Designing",
      desc: "Create visual concepts to help others interpret the world around them through color, texture, images, and symbols.",
      img: "./assets/graphic.jpg",
      link: "https://drive.google.com/drive/folders/1pVL7I_PTEtbE33TcVETD0tFvQnTAbk2P"
    },
  ];

  const handleClick = (way) => {
    way === "left"
      ? setCurrentSlide(currentSlide > 0 ? currentSlide - 1 : 2)
      : setCurrentSlide(currentSlide < data.length - 1 ? currentSlide + 1 : 0);
  };

  return (
    <div className="works" id="works">
      <div
        className="slider"
        style={{ transform: `translateX(-${currentSlide * 100}vw)` }}
      >
        {data.map((d) => (
          <div className="container">
            <div className="item">
              <div className="left">
                <div className="leftContainer">
                  <div className="imgContainer">
                    <img src={d.icon} alt="" />
                  </div>
                  <h2>{d.title}</h2>
                  <p>{d.desc}</p>
                  <a href={d.link} >
                    <span>Projects</span>
                  </a>
                </div>
              </div>
              <div className="right">
                <img
                  src={d.img} alt=""/>
              </div>
            </div>
          </div>
        ))}
      </div>
      <img
        src="assets/arrow.png"
        className="arrow left"
        alt=""
        onClick={() => handleClick("left")}
      />
      <img
        src="assets/arrow.png"
        className="arrow right"
        alt=""
        onClick={() => handleClick()}
      />
    </div> 
  );
}
